.featured {
  height: 100vh;
}
.featured--vertical {
  width: inherit;
  height: inherit;
  background: linear-gradient(to top, #111 10%, transparent 90%);
}
.featured--horizontal {
  width: inherit;
  height: inherit;
  background: linear-gradient(to right, #111, transparent );
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 30px;
  padding-bottom: 150px;
}
.featured--name {
  font-size: 60px;
  font-weight: bold;
}
.featured--info {
  font-size: 18px;
  font-weight: bold;
  margin-top: 15px;
}

.featured--points,
.featured--seasons,
.featured--year {
  display: inline-block;
  margin-right: 15px;
}
.featured--points {
  color: #46d369;
}

.featured--description {
  margin-top: 15px;
  font-size: 20px;
  color: #999;
  max-width: 40%;
}

.featured--buttons {
  margin-top: 15px;
}

.featured--watchbutton,
.featured--mylistbutton {
    display: inline-block;
    font-size: 20px;
    font-weight: bold;
    padding: 12px 20px;
    border-radius: 5px;
    text-decoration: none;
    margin-right: 10px;
    transition: all(0.2);
}
.featured--mylistbutton:hover,
.featured--watchbutton:hover{
    opacity: 0.7;
}

.featured--watchbutton{
    background-color: #fff;
    color: #000;
}
.featured--mylistbutton{
    background-color: #999;
    color: #fff;
}
.featured--genres{
    margin-top: 15px;
    font-size: 18px;
    color: #999;
}